import React from 'react';
import SkillsLogosWrapper from './SkillsLogosWrapper';
import Gears from './Gears';

import './skills.css';

import css3 from './images/css3.svg';
import django from './images/django.svg';
import git from './images/git.svg';
import github from './images/github.svg';
import html5 from './images/html5.svg';
import javascript from './images/javascript.svg';
import python from './images/python.svg';
import react from './images/react.svg';
import sass from './images/sass.svg';
import vue from './images/vue.svg';
import nuxt from './images/nuxt.svg';
import sails from './images/sails.svg';
import digitalOcean from './images/digitalOcean.svg';
import aws from './images/aws.svg';

const HTML = {
    containerTitle: "HTML",
    'HTML5': html5,
    'JSX': react
}

const CSS = {
    containerTitle: "CSS",
    'CCS3': css3,
    'SASS': sass
}

const JAVASCRIPT = {
    containerTitle: "JavaScript",
    'JavaScript': javascript,
    'Vue.js': vue,
    'Nuxt.js': nuxt,
    'React': react,
}

const BACKEND = {
    containerTitle: "Back-End",
    "Sails.js": sails,
    'Python': python,
    'Django': django,
}

const TOOLS = {
    containerTitle: 'Tools',
    'GIT': git,
    'GitHub': github,
    "DO": digitalOcean,
    "S3": aws
}

const Skills = () => {
    return (
        <>
            <div className="split">
                <SkillsLogosWrapper skillsGroup={JAVASCRIPT} />
                <SkillsLogosWrapper skillsGroup={HTML} />
                <SkillsLogosWrapper skillsGroup={CSS} />
                <SkillsLogosWrapper skillsGroup={BACKEND} />
                <SkillsLogosWrapper skillsGroup={TOOLS} />
            </div>
            <div className="split">
                <Gears />
            </div>
        </>
    )
}

export default Skills;